.test-features-container {
  flex-direction: column;
  align-items: center;
  gap: 40px;
  height: 100vh;
  padding: 40px;
  display: flex;
}

.group-box {
  text-align: center;
  background: #000000b3;
  border: 3px solid #0fc;
  border-radius: 12px;
  width: 80%;
  max-width: 1200px;
  padding: 40px;
}

.grid-wrapper {
  perspective: 1200px;
  cursor: grab;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.grid-wrapper:active {
  cursor: grabbing;
}

.grid-container {
  width: 600px;
  height: 600px;
  transform-style: preserve-3d;
  transform-origin: center;
  margin-top: -300px;
  margin-left: -300px;
  transition: transform .1s linear;
  position: absolute;
  top: 50%;
  left: 50%;
}

.grid-cell {
  transform-origin: center;
  background: #0096ffb3;
  border: 2px solid #ffffff4d;
  width: 40px;
  height: 40px;
  transition: all .2s;
  position: absolute;
}

.grid-cell:hover {
  z-index: 100;
  background: #ffc800e6;
  transform: scale(1.3);
}
